import {AfterViewInit, Component, ElementRef, OnInit, ViewChild} from '@angular/core';
// import Chart from 'chart.js/auto';
import {ChatroomService} from "../../../services/chatroom.service";
import {UserService} from "../../../services/user.service";
import {ToastrService} from "ngx-toastr";

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  chats: any[]=[];
  clients: any[]=[];
  messages: any[] = [];
  messageCharts: any[] = [];
  userUse: any[] = [];
  numberOfMessages: number | undefined;

  @ViewChild('myChart') myChart!: ElementRef;

  //config chart;
  view: [number, number] = [660, 300];

  showXAxis = true;
  showYAxis = true;
  gradient = false;
  xAxisLabel = 'Thng';
  yAxisLabel = 'S lng tin  nhn';
  showLegend = true;

  constructor(private chatRoomService: ChatroomService, private userService: UserService, private toastr: ToastrService) { }

  ngOnInit() {
    this.getAllChatRoomAdmin();
    this.getAllMessagesAdmin();
    this.getTopClientsChat();
  }

  onSelect(event: any) {
    console.log(event);
  }

  getTopClientsChat() {
    this.userService.getAllClients().subscribe({
      next: (response: any) => {
        this.clients = response;

        let clientMap = new Map();
        for (let client of response) {
          clientMap.set(client.id, { ...client, messageCount: 0 });
        }
        // Lc v thm thuc tnh message vo user nu c id ph hp
        for (let message of this.userUse) {
          let userId = message.senderId;
          if (clientMap.has(userId)) {
            clientMap.get(userId).messageCount = message.messageCount;
          }
        }

        this.clients = Array.from(clientMap.values());
        this.clients.sort((a, b) => {
          if(a.messageCount > b.messageCount) return -1;
          if(a.messageCount < b.messageCount) return 1;
          return 0;
        })

        this.clients = this.clients.slice(0, 10);
      }
    })
  }

  getAllChatRoomAdmin(){
    this.chatRoomService.getAllChatAdmin().subscribe({
      next: (response: any) => {
        this.chats = response;
      }
    })
  }
  getAllMessagesAdmin() {
    this.chatRoomService.getAllMessagesAdmin().subscribe({
      next: (response: any) => {
        this.messages = response;

        // m s lng tin nhn theo thng
        let data = this.messages.reduce((acc, item) => {
          const date = new Date(item.createdAt);
          const monthYear = `${date.getMonth() + 1}-${date.getFullYear()}`; // To chui thng-nm

          if (!acc[monthYear]) {
            acc[monthYear] = { name: monthYear, value: 0,  realDate: item.createdAt}; // Khi to i tng mi nu cha tn ti
          }

          acc[monthYear].value++; // Tng s lng bn ghi

          return acc;
        }, {});

        this.messageCharts = Object.values(data);
        this.messageCharts.sort((a, b) => {
          if (a.realDate < b.realDate) return -1;
          if (a.realDate > b.realDate) return 1;
          return 0;
        });

        // m s lng tin nhn theo senderId
        let dataBySender = this.messages.reduce((acc, item) => {
          const senderId = item.senderId;

          if (!acc[senderId]) {
            acc[senderId] = { senderId: senderId, messageCount: 0 };
          }

          acc[senderId].messageCount++;

          return acc;
        }, {});

        this.userUse = Object.values(dataBySender);

        // m s lng tin  nhn trong 7 ngy qua
        // Ly thi im hin ti
        const currentDate = new Date();
        // S lng tin nhn trong 7 ngy qua
        const messagesWithinLast7Days = this.messages.filter(item => {
          // Chuyn thi im to tin nhn thnh i tng Date
          const messageDate = new Date(item.createdAt);

          // Tnh khong thi gian gia thi im hin ti v thi im to tin nhn
          const timeDifference = currentDate.getTime() - messageDate.getTime();

          // Chuyn thi gian t milliseconds sang ngy
          const daysDifference = timeDifference / (1000 * 3600 * 24);

          // Kim tra xem tin nhn c c to trong vng 7 ngy qua khng
          return daysDifference <= 7 && daysDifference > 0;
        });

        // S lng tin nhn trong 7 ngy qua
        this.numberOfMessages = messagesWithinLast7Days.length;
      },
      error: err =>{
        this.toastr.error('', err, {
          timeOut: 2000,
        });
      },
    })
  }
}
